@import url(https://fonts.googleapis.com/css?family=Noto+Sans&display=swap);
header {
    display: flex;
    width: 100%;
    background-color:white;
    height: 44px;
    z-index: 400;
    position: fixed;
    top: 0;
    width: 100%;
    padding: 15px;  
}

/***** Light Theme *****/
.header-light {
    background-color: white;
    border-bottom: solid 1px #F0F2F5;
}

/***** Dark Theme *****/
.header-dark {
    background-color: #242526;
    border-bottom: solid 1px #3A3B3C;
}

header > label {
    align-self: center;
    margin-left: auto;
    margin-right: 50px;
}

input[type=checkbox] {
    -webkit-appearance: none;
    visibility: hidden;
    display: none;
}

.check {
    position: relative;
    display: block;
    width: 40px;
    height: 20px;
    background: #092c3e;
    cursor: pointer;
    border-radius: 20px;
    border: solid black 1px;
    overflow: hidden;
    transition: ease-in 0.5s;
}


input[type=checkbox]:checked ~ .check { 
    background: white;
}

.check::before {
    content: '';
    position: absolute;
    top: 2px;
    left: 2px;
    background: white;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    transition: 0.5s;
}

input[type=checkbox]:checked ~ .check::before {
    transform: translateX(20px);
    background: #092c3e;
}
div {
    top: 5px;
    position: relative;
}

div > span {
    position: absolute;
    top: 10px;
    left: 15px;
}

div > input[type=text] {
    width: 300px;
    height: 2.5em;
    padding-left: 40px;
    border-radius: 25px;
    text-indent: 10px;  
    border: 0px;
}

div > input[type=text]:focus{
    outline: none;
}

/***** Light Theme *****/
.search-container > span {
    color: #656C75;
}

.search-container > input[type=text] {
    background-color: #F0F2F5;
    color: #656C75;
}

.search-container > input[type=text]::-webkit-input-placeholder{
    color: #656C75;
}

.search-container > input[type=text]:-ms-input-placeholder{
    color: #656C75;
}

.search-container > input[type=text]::-ms-input-placeholder{
    color: #656C75;
}

.search-container > input[type=text]::placeholder{
    color: #656C75;
}

/***** Dark Theme *****/
.search-container-dark > span {
    color: #ACAFB4;
}

.search-container-dark > input[type=text] {
    background-color: #3A3B3C;
    color: #ACAFB4;
}

.search-container-dark > input[type=text]::-webkit-input-placeholder  {
    color: #ACAFB4;
}

.search-container-dark > input[type=text]:-ms-input-placeholder  {
    color: #ACAFB4;
}

.search-container-dark > input[type=text]::-ms-input-placeholder  {
    color: #ACAFB4;
}

.search-container-dark > input[type=text]::placeholder  {
    color: #ACAFB4;
}
body {
    margin: 0;
    padding: 0;
}

.image-gallery {
    padding-top: 100px;  
    padding-bottom: 50px;
}
   
ul {
    margin: 0;
    padding-left: 30px;
    padding-right: 30px;
    display: grid;
    grid-gap: 1rem;
    gap: 1rem;
    list-style-type: none;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-auto-rows: 250px;
}

li {
    display: flex;
    justify-content: center;
    align-items: center;
}

img {
    max-width: 100%;
    max-height:100%;
    width: auto;
    height: auto;
    border-radius: 15px;
    transition: all 500ms;
    overflow: hidden;
}

img:hover {
    box-shadow: rgba(2, 8, 20, 0.1) 0px 0.35em 1.175em, rgba(2, 8, 20, 0.08) 0px 0.175em 0.5em;
    transform: translateY(-3px) scale(1.1);
}
